export default {
  data() {
    return {
      user: {},
    }
  },
  async fetch() {
    this.user = await fetch(
      'https://f7b0efc1-99d2-4a67-a280-f2632dc7ed81.mock.pstmn.io/users'
    )
      .then((res) => res.json())
      .then(({ attributes }) =>
        attributes.find(({ _id }) => _id == this.$route.params.id)
      )
  },
  methods: {
    async submit() {
      await fetch(
        'https://f7b0efc1-99d2-4a67-a280-f2632dc7ed81.mock.pstmn.io/users',
        {
          method: 'PATCH',
          body: this.user,
        }
      )
        .then((res) => res.json())
        .then(({ status }) => {
          status == 'success' &&
            this.$router.push({
              path: '/',
            })
        })
    },
    async deleteUser() {
      await fetch(
        'https://f7b0efc1-99d2-4a67-a280-f2632dc7ed81.mock.pstmn.io/users?_id=' +
          this.user._id,
        {
          method: 'DELETE',
        }
      )
        .then((res) => res.json())
        .then(({ status }) => {
          status == 'success' &&
            this.$router.push({
              path: '/',
            })
        })
    },
  },
}
